.dashboard {
  display: flex;
  flex-direction: column;
  height: 100vh;

  .container {
    height: calc(100vh - 350px);
    padding-top: $container-padding-vertical;
    padding-bottom: $container-padding-vertical;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    padding-bottom: 20px;

    h1 {
      margin: 0 0 30px 0;
      font-size: 1.5rem;
    }

    .score {
      width: 100%;
      height: 20px;

      &__total {
        width: 100%;
        height: 100%;
        background-color: $color-white;
        border-radius: 20px;
        overflow: hidden;
        display: block;
      }

      &__cursor {
        height: 100%;
        width: 100%;
        background-color: $color-yellow;
        display: block;
        transform: scaleX(0.5);
        transform-origin: left;
        transition: transform 0.5s ease-out;
      }

      &__end {
        display: flex;
        width: 100%;
        height: 1px;

        .end {
          width: 50%;
          height: 1px;
          transition: width 0.5s ease-out;
          position: relative;

          .img {
            position: absolute;
            right: 0;
            transform: translate(50%,-56%);
          }
        }
      }
    }
  }
}

@media (max-width: $breakpoints-small) {
  .dashboard {  
    .container {
      height: calc(100vh - 260px);
    }
  }
}
