.room-page {
  position: relative;

  p {
    margin: 0 30px;
    text-align: center;
  }

  &__img {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100vw;
    z-index: -1;
    display: flex;
    max-height: 210px;

    svg {
      width: 100%;
      height: 100%;
    }
  }

  .container-button {
    margin-top: 120px;

    .button {
      div {
        background-color: $color-dark-blue;
        width: fit-content;
        margin: auto;
        padding: 15px 60px;
      }
    }
  }
}
